import React, {useState} from 'react'
import './room.css'
import salaA from '../offer/sala2.jpg'

const RoomA = () => {
    const [isShown, setIsShown] = useState(true)   

    return (
        <div className="room">
            <div className="room-content">
                {isShown &&
                <div className="room-title">Sala A</div>}
                <div className="room-left">
                    <img 
                    onClick={() => setIsShown(!isShown)} 
                    src={salaA}
                    alt="Sala prób mniejsza próbówka music house wrocław"/>
                </div>
                {isShown && (
                <div className="room-right sala-a">
                    <div className="room-right-up">Powierzchnia 25 metrów kwadratowych, klimatyzowana, ogrzewana</div>
                    <div className="room-right-down">
                    <div className="labels">
                            <div className="label">Gitara 1:</div>
                            <div className="label">Gitara 2:</div>
                            <div className="label">Bas:</div>
                            <div className="label">Perkusja:</div>    
                            <div className="label">Przody:</div>
                            <div className="label">Monitory:</div>
                            <div className="label">Mikser:</div>
                            <div className="label">Mikrofony:</div>
                        </div>
                        <div className="items">
                            <div className="item">Laboga Beast 30W Plus + Paczka Laboga 212a</div>
                            <div className="item">Fender Champion 100 2x12</div>
                            <div className="item">Ashdown Mag EVO II 300 1x15</div>    
                            <div className="item">Mapex Armory Studioease Fast</div>
                            <div className="item">bB Technologies B-Hype 10</div>
                            <div className="item">the box MA100 (2 na salkę)</div>
                            <div className="item">Soundcraft EPM 6</div>
                            <div className="item">Shure SM 58</div>
                        </div>  
                    </div>
                </div>)}
            </div>
            
        </div>
    )
}

export default RoomA
