import React from 'react'
import './pricing.css'
import Title from '../section-title/Title'

const Pricing = () => {
    return (
        <div className="pricing" id="pricing">
            <Title title="Cennik sali prób i studio nagrań Próbówka Music House Wrocław (niezależnie od wybranej sali)."/>
                <div className="pricing-content">
                    <div className="pricing-down">
                        <div className="pricing-left">
                            <div className="pricing-left-top">
                                <h3>Sale [rezerwacja on-line; płatność on-line lub gotówką]</h3><br></br>
                                1 godzina = 35 zł <br></br>
                                2 godziny = 60 zł <br></br>
                                3 godziny = 85 zł <br></br>
                                Każda następna + 20 zł/h<br></br><br></br>

                                Talerze - 10 zł / próba (w tym wypadku płatność tylko gotówka w salce)<br></br>
                                Gitara / bas - bezpłatnie<br></br>
                                Abonamenty: do uzgodnienia <br></br><br></br>
                                Rabaty: pn-pt od 6 do 15: za każdą godzinę 20 zł (w tym wypadku płatność tylko gotówką w salce) <br></br>
                            </div>
                            <div className="pricing-left-down">
                                <h3>Studio [rezerwacja telefoniczna; płatność on-line lub gotówką]</h3><br></br>
                                Studio: 50 zł / h (z realizatorem) <br></br> 
                                Cały dzień (10h): 500 zł<br></br><br></br>
                                Mix i mastering od 100 zł utwór<br></br>
                                Przykładowe nagrania: <a href="https://soundcloud.com/piotr-maczynski"> https://soundcloud.com/piotr-maczynski</a>
                            </div>
                        </div>
                        <div className="pricing-right">
                            <div style={{color: "white"}}>
                                <h3>Inne [rezerwacja telefoniczna; płatność on-line lub gotówką]</h3>
                            <br></br>
                            </div>
                            <div>
                            Nagrywanie próby z sound-checkiem przeprowadzonym przez realizatora: 45 zł /h min. 2h<br></br><br></br>
                            Nagranie live 45 minut w salce (możliwość zaproszenia publiki):<br></br>
                            + mix i mastering trzech wybranych utworów<br></br> 
                            + dogrywka wokalu w razie potrzeby <br></br>
                            + pliki video z kamer <br></br>
                            + wrzucenie nagrania na profil sali prób na serwisach streamingowych:<br></br> 800 zł przy standardowym składzie zespołu (2x gitara, bas, wokal, perkusja)<br></br>
                            Przykładowe nagranie: <a href="https://soundcloud.com/izolacja">https://soundcloud.com/izolacja</a><br></br><br></br>
                            Nagrywanie koncertu live w klubie: do uzgodnienia.
                            </div>
                        </div>
                    </div>
                    <div className="pricing-up">
                    <h3>Potrzebujesz rachunku - nie ma sprawy - działamy legalnie.</h3>
                        </div>
                </div>
            </div>
    )
}

export default Pricing
